<template>
  <div class="ViewWall">

    <kedja-header>
      <button @click="createCollection">
        + Ny samling
      </button>
    </kedja-header>

    <div class="content">
      <wall :wall="wall" class="mywall"></wall>
      <context-menu class="cxtMenu" v-model="menuOpen" :style="{'flex-basis': menuOpen ? '300px' : '0px'}"></context-menu>
      <!--context-menu class="cxtMenu" v-model="menuOpen"></context-menu-->
      <!--div class="wlls">
      </div-->
      <!--div class="menu">
      </div-->
    </div>

  </div>
</template>

<script>

import KedjaHeader from '@/components/layout/KedjaHeader'
import Wall from '@/components/kedja/Wall'
import ContextMenu from '@/components/contextMenu/ContextMenu'

export default {
  name: 'ViewWall',
  components: {
    KedjaHeader,
    Wall,
    ContextMenu
  },
  data () {
    return {
      menuOpen: ""
    }
  },
  props: {
  },
  computed: {
    wall: function () {
      return this.$store.getters.getActiveWall();
    },
    sidebarFlex: function () {

    }
  },
  methods: {
    getWallFromParam: function () {
      let wallId = this.$route.params['wallId'];
      if(wallId){
        let params = {
          //endpoint: wallId + "/wall",
          endpoint: 'walls/' + wallId,
          successCallback: (data) => {
            this.$store.commit('setActiveWall',{wall: data.data});
          },
        }
        this.$store.commit('makeAPICall', params);
      }
    },
    createCollection: function () {
      //this.$store.commit('createCollectionInWall',{wall: this.wall});
      let params = {
        endpoint: "walls/" + this.wall.rid + "/collections",
        data: {title: 'Ny samling'},
        method: "post",
        successCallback: (data) => {
          console.log(data)
          this.wall.collections.push(data.data)
        }
      }

      this.$store.commit('makeAPICall',params);
    },
  },
  mounted: function () {
    this.getWallFromParam();
  }
}
</script>

<style scoped>

.ViewWall{

  /* LAYOUT */
  display: flex;
  flex-direction: column;
  transition: all 0.5s;
}

.content{

  /*vertical flex*/
  flex: 1;
  overflow: hidden;

  /* LAYOUT */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /*transition: all 0.5s;*/

}

.mywall{
  flex: 1;
}

.cxtMenu{
  flex: 0 0;
  border: 1px solid lightgray;
  overflow-y: scroll;
  transition: all 0.5s;
}

.wlls{
  flex: 1;
  background: green;
}

.menu{
  flex: 0 0 400px;
  background: red;
}

</style>



